// components/HotDeals.tsx
import React from 'react';
import { Card, Button, Typography, Image } from 'antd';
import type { CSSProperties } from 'react';


const { Text } = Typography;

export type HotDeal = {
  id: string;
  title: string;
  description?: string;
  image: string; // URL or /images/xxx.jpg
  price: string;
};

const cardStyle: CSSProperties = {
  borderRadius: 12,
  overflow: 'hidden',
  padding: 16, 
  textAlign: 'center'
};

export default function HotDeals({ deals }: { deals: HotDeal[] }) {
  return (
    <section className="hot-deals-section container" aria-labelledby="hot-deals-title">
      <div style={{ textAlign: 'center', marginBottom: 28 }}>
        <h2 id="hot-deals-title">This Month's Hot Deals</h2>
        <p className="hot-deals-subtitle">Unmissable offers on selected premium statues.</p>
      </div>

      {/* Image.PreviewGroup 支持在同一组内左右切换预览 */}
     
    
        <div className="hot-deals-grid" style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit,minmax(220px,1fr))', gap: 18 }}>
          {deals.map((d) => (
            <Card key={d.id} variant={'outlined'} style={cardStyle} >
              {/* 使用 Antd Image：点击图片会打开预览大图 */}
              <div style={{ display: 'flex', justifyContent: 'center' }}>
                <Image
                  src={d.image}
                  alt={d.title}
                  width={180}
                  height={180}
                  style={{ objectFit: 'cover', borderRadius: '50%' }}
                  // 可通过 preview 对象进一步定制（比如 mask 、icons）
                  preview={{ mask: <></> }}
                />
              </div>

              <h4 style={{ marginTop: 12 }}>{d.title}</h4>
              {d.description && <span style={{ display: 'block', marginBottom: 8 }}>{d.description}</span>}
              <div style={{ marginTop: 10, fontWeight: 700, color: 'var(--accent1)' }}>{d.price}</div>
              <Button type="primary" style={{ marginTop: 12 }}>Buy Now</Button>
            </Card>
          ))}
        </div>
    </section>
  );
}